.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.active{
  color:red;
}
.icon.yellow-text {
  margin: 2%;
}

.row.no-margin.grey.darken-4{
  margin: 0;
}

.col.s9.no-pad-left{
  padding: 0%;
}

.center.no-margin{
  margin: 0%;
  padding-top: 10%;
}
.margin-both{
  margin: 0 9% 0 9%;
}
